import React from 'react'
import './Rules.css'
import Rulesimg from '../images/image-rules-bonus.svg'
import Close from '../images/icon-close.svg'


function Rulesfull({ setRules, rules }) {
    return (
        <div className="rulesfull__container" style={{zIndex: rules ? "-1" : "3"}}>
            <p>RULES</p>
            <img src={Rulesimg} className="rulesimg" alt="" />
            <img src={Close} className="close" alt="" onMouseUp={() => setRules(!rules)} />
        </div>
    )
}

export default Rulesfull
