import { React, useState } from 'react';

import './App.css';
import Score from "./Components/Score"
import Board from "./Components/Board"
import Rules from "./Components/Rules"
import Rulesfull from "./Components/Rulesfull"
function App() {
  const [rules, setRules] = useState(true);
  const [score, setScore] = useState(0)
  
  return (
    <div className="App">
      <span className="dark__cover" style={{opacity: rules ? 0: 1, zIndex: rules ? -1 : 2}}></span>
      <Score score={score}/>
      <Board score={score} setScore={setScore}/>
      <Rules  setRules={setRules} rules={rules}/>
      <Rulesfull setRules={setRules} rules={rules}/>
    </div>
  );
}

export default App;

