import React, {useState} from 'react'
import './Board.css'
import Scissors from '../images/icon-scissors.svg'
import Paper from '../images/icon-paper.svg'
import Rock from '../images/icon-rock.svg'
import Lizard from '../images/icon-lizard.svg'
import Spock from '../images/icon-spock.svg'
function Board({score, setScore}) {
    const [pick, setPick] = useState([])
    const [pickcomp, setPickcomp] = useState([])
    const [picking, setPicking] = useState(true)
    const [result, setResult] = useState(false)
    const [result2, setResult2] = useState(false)
    const [result3, setResult3] = useState(false)
    const [show, setShow] = useState(false)
    const [end, setEnd] = useState("") 


    function timer(inc) {
        setTimeout(function () {
            if(inc === 1)
                setScore(score => score + 1)
            else
                if(score > 0)
                setScore(score => score - 1)
        }, 2000)
    }
    const picked = (e) => {
        setPicking(false)
        setTimeout(function () { setResult(true) }, 1000)
        setTimeout(function () { setResult2(true) }, 1500)
        setTimeout(function () { setResult3(true) }, 2000)
        var randomNumber = Math.floor(Math.random() * (4 - 0 + 1)) + 0;
        let image = ""
        let radial = ""
        let color = ""
        switch (randomNumber) {
            case 0:
                image = Scissors; radial = "#ec9e0e, #eca922"; color = "#eca92280"
                if (pick.image === Scissors) setEnd("DRAW")
                else if (pick.image === Paper || pick.image === Lizard) {
                    setEnd("YOU WIN"); timer(1);
                }
                else {
                    setEnd("YOU LOSE"); timer(2);
                }
                break;
            case 1:
                image = Paper; radial = "#4865f4, #5671f5"; color = "#5671f580"
                if (pick.image === Paper) setEnd("DRAW")
                else if (pick.image === Rock || pick.image === Spock) {
                    setEnd("YOU WIN"); timer(1);
                }
                else {
                    setEnd("YOU LOSE"); timer(2);
                }
                break;
            case 2:
                image = Rock; radial = "#dc2e4e, #dd405d"; color = "#dd405d80"
                if (pick.image === Rock) setEnd("DRAW")
                else if (pick.image === Lizard || pick.image === Scissors) {
                    setEnd("YOU WIN"); timer(1);
                }
                else {
                    setEnd("YOU LOSE"); timer(2);
                }
                break;
            case 3:
                image = Lizard; radial = "#834fe3, #8c5de5"; color = "#8c5de580"
                if (pick.image === Lizard) setEnd("DRAW")
                else if (pick.image === Spock || pick.image === Paper) {
                    setEnd("YOU WIN"); timer(1);
                }
                else {
                    setEnd("YOU LOSE"); timer(2);
                }
                break;
            case 4:
                image = Spock; radial = "#40b9ce, #52bed1"; color = "#52bed180"
                if (pick.image === Spock) setEnd("DRAW")
                else if (pick.image === Scissors || pick.image === Rock) {
                    setEnd("YOU WIN"); timer(1);
                }
                else {
                    setEnd("YOU LOSE"); timer(2);
                }
                break;
            default:
                break;
        }
        setPickcomp({ image: image, radial: radial, color:color})
        setTimeout(function(){ setShow(true) },2000)
    }

    const restartClicked = (e) => {
        e.preventDefault()

        setResult(false)
        setResult2(false)
        setResult3(false)
        
        setTimeout(function () {
            setPicking(true)
            setShow(false)
        }, 1000)
    }

    function lapicked(image, radial, color) {
        setPick({
            image: image,
            radial: radial,
            color: color
        })
    }
    return (
        <div className="board__container">
        
            <div className="picking__side" style={{opacity: picking ? 1: 0}}>
                <div className={`${picking ? "" : "divtran"}`} onMouseDown={()=>lapicked(Scissors, "#ec9e0e, #eca922","#eca92280")} onMouseUp={picked} style={{ '--i': "0deg", "--c":"#eca92280"}}>
                    <span className={picking ? "" : "circtran"} style={{"--rotate": "0deg", "--rad": "#ec9e0e, #eca922" }}>
                        <span className="box" style={{backgroundImage: `url(${Scissors})`}}></span>
                    </span></div>
                <div onMouseDown={()=>lapicked(Paper, "#4865f4, #5671f5","#5671f580")} onMouseUp={picked} style={{ '--i': "75deg", "--c":"#5671f580" }}>
                    <span className={picking ? "" : "circtran"} style={{"--rotate": "-75deg", "--rad": "#4865f4, #5671f5" }}>
                        <span className="box" style={{backgroundImage: `url(${Paper})`}}></span>
                    </span></div>
                <div onMouseDown={()=>lapicked(Rock, "#dc2e4e, #dd405d","#dd405d80")} onMouseUp={picked} style={{ '--i': "145deg", "--c":"#dd405d80" }}>
                    <span className={picking ? "" : "circtran"} style={{"--rotate": "-145deg", "--rad": "#dc2e4e, #dd405d" }}>
                        <span className="box"  style={{backgroundImage: `url(${Rock})`}}></span>
                    </span></div>
                <div onMouseDown={()=>lapicked(Lizard, "#834fe3, #8c5de5","#8c5de580")} onMouseUp={picked} style={{ '--i': "215deg", "--c":"#8c5de580" }}>
                    <span className={picking ? "" : "circtran"} style={{"--rotate": "-215deg", "--rad": "#834fe3, #8c5de5" }}>
                        <span className="box"  style={{backgroundImage: `url(${Lizard})`}}></span>
                    </span></div>
                <div onMouseDown={()=>lapicked(Spock, "#40b9ce, #52bed1","#52bed180")} onMouseUp={picked} style={{ '--i': "285deg", "--c":"#52bed180" }}>
                    <span className={picking ? "" : "circtran"} style={{"--rotate": "-285deg", "--rad": "#40b9ce, #52bed1" }}>
                        <span className="box"  style={{backgroundImage: `url(${Spock})`}}></span>
                    </span></div>
            </div>
            <div className={`${result ? "resulttran" : ""} result__side `}>
                <section className={`${result2 ? "width100" : ""} result`} >
                    <span>
                        <p>YOU PICKED</p>
                        <span className="circle" style={{'--c': pick.color, '--rad': pick.radial, animationName: result3 && (end ==="YOU WIN")  ? "circanim" : ""}}>
                            <span className="box" style={{backgroundImage: `url(${pick.image})`}}></span>
                        </span>
                    </span>
                    <span>
                        <p>THE HOUSE PICKED</p>
                        {show ?
                            <span className="circle" style={{ '--c': pickcomp.color, '--rad': pickcomp.radial, animationName: result3 && (end ==="YOU LOSE") ? "circanim" : ""}}>
                                <span className="box" style={{backgroundImage: `url(${pickcomp.image})`}}></span>
                            </span> : ""}
                    </span>
                </section>
                <section className={`buttons ${result3 ? "buttonstran" : ""}`}>
                    <p>{end}</p>
                    <button onMouseUp={restartClicked}>PLAY AGAIN</button>
                </section>
            </div>
        </div>
    )
}
export default Board
